<template>
  <div class="searchsuggest">
    <div class="search">
      <div class="btn" @click="$emit('exit-searchsuggest', false)">
        <img src="../assets/searchsuggest-exit.png" alt="" />
      </div>
      <input
        type="text"
        :placeholder="searchHot?.sketch?.data?.frontword"
        v-model="value"
        @keyup.enter="addhistory"
        ref="input"
        @focus="$refs.input.style.border = '1rem solid #ff5777'"
        @blur="$refs.input.style.border = '1rem solid #aaa'"
      />
      <p>搜索</p>
    </div>

    <div class="history">
      <p class="historytitle">
        <i class="icon"></i> 历史搜索
        <span class="delete" @click="history = []"></span>
      </p>
      <ul class="list">
        <li v-if="!history.length"><p class="active">暂无历史搜索</p></li>
        <li v-else v-for="h in history" :key="h" @click="add(h)">
          <p>{{ h }}</p>
        </li>
      </ul>
    </div>

    <div class="hot">
      <p class="hottitle"><i class="icon"></i> 热门搜索</p>
      <ul class="list">
        <li
          v-for="item in searchHot?.hotWord?.data"
          :key="item.frontword"
          @click="add(item.frontword)"
        >
          <p :class="item.color ? 'active' : ''">
            {{ item?.frontword }}
          </p>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      history: JSON.parse(localStorage.getItem("history")) || [],
      value: "",
    };
  },
  created() {
    this.$store.dispatch("getsearchHot");
  },
  mounted() {
    this.$refs.input.focus();
  },
  computed: {
    ...mapState(["searchHot"]),
  },
  methods: {
    addhistory: function () {
      if (this.value != "") {
        this.history = [...new Set([this.value, ...this.history])];
        this.$router.push({
          path: "/searchresult",
          query: { keyword: this.value },
        });
      }
    },
    add: function (value) {
      this.history = [...new Set([value, ...this.history])];
      if (this.$route.path != "/searchresult") {
        this.$router.push({
          path: "/searchresult",
          query: { keyword: value },
        });
      } else {
        this.$router.replace({
          path: "/searchresult",
          query: { keyword: value },
        });
      }
      this.$nextTick(() => {
        this.$emit("noshow-searchsuggest", false);
      });
    },
  },
  watch: {
    history: function (newhistory) {
      console.log("history变化了");
      localStorage.setItem("history", JSON.stringify(newhistory));
    },
  },
};
</script>

<style lang="less" scoped>
.searchsuggest {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background: white;
  z-index: 9;
  .search {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 45rem;
    padding: 5rem 0;
    div.btn {
      img {
        width: 26rem;
        height: 20rem;
      }
    }
    input {
      width: 295rem;
      height: 25rem;
      border: 1rem solid #aaa;
      border-radius: 4.5rem;
      font-size: 14rem;
      line-height: 25rem;
      background: transparent;
      outline: none;
      &.active {
        border: 1rem solid #ff5777;
      }
    }
    p {
      font-size: 14rem;
      color: #666666;
    }
  }

  div.history {
    padding: 10rem;
    .historytitle {
      width: 100%;
      font-size: 12rem;
      color: #999999;

      i {
        float: left;
        height: 15rem;
        width: 15rem;
        margin-right: 2px;
        background: url(../assets/searchsuggest-search.png) center center
          no-repeat;
        background-size: contain;
      }
      span.delete {
        float: right;
        height: 15rem;
        width: 15rem;
        margin-right: 0.2rem;
        background: url(../assets/searchsuggest-deleteicon.png) center center
          no-repeat;
        background-size: contain;
      }
    }

    ul.list {
      display: flex;
      flex-wrap: wrap;

      li {
        height: 25rem;
        margin: 12rem 0 0 10rem;
        p {
          border-radius: 5rem;
          border: 1rem solid #f5f5f5;
          font-size: 12rem;
          line-height: 25rem;
          color: #666666;
          padding: 0 11rem;
          &.active {
            color: #999999;
            border: 0;
          }
        }
      }
    }
  }

  div.hot {
    padding: 10rem;
    .hottitle {
      font-size: 12rem;
      color: #999999;
      display: flex;

      i {
        height: 15rem;
        width: 15rem;
        margin-right: 2px;
        background: url(../assets/searchsuggest-start.png) center center
          no-repeat;
        background-size: contain;
      }
    }

    ul.list {
      display: flex;
      flex-wrap: wrap;

      li {
        height: 25rem;
        margin: 12rem 0 0 10rem;
        border: 1rem solid #f5f5f5;
        border-radius: 5rem;
        p {
          font-size: 12rem;
          line-height: 25rem;
          color: #666666;
          padding: 0 11rem;
          &.active {
            color: #ff4466;
          }
        }
      }
    }
  }
}
</style>