<template>
  <div class="classify">
    <div class="search">
      <div class="im-talk">
        <svg t="1665056351788" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"
          p-id="3028" width="35" height="35" @click="$router.back()">
          <path
            d="M608 736c-6.4 0-19.2 0-25.6-6.4l-192-192C384 524.8 384 499.2 390.4 486.4l192-192c12.8-12.8 32-12.8 44.8 0s12.8 32 0 44.8L460.8 512l166.4 166.4c12.8 12.8 12.8 32 0 44.8C627.2 736 614.4 736 608 736z"
            p-id="3029" fill="#adadad"></path>
        </svg>
      </div>
      <div class="input" @click="showSearchSuggest = true">
        <img src="../assets/home-search.png" alt="" />
        <input type="text" placeholder="关键词" />
      </div>
      <div class="cart" @click="$router.push('/shopcart')"></div>
    </div>

    <div class="nav">
      <van-sidebar v-model="activeKey" class="mainnav">
        <van-sidebar-item v-for="navname in classifyMainNav" :key="navname._system_record_entry_id"
          :title="navname.title" @click="
  fcid = navname?.miniWallkey;
sort = 'pop';
pid = navname?.maitKey;
          " />
      </van-sidebar>

      <div class="content">
        <ul class="secondnav">
          <li v-for="secondnav in classifySecondNav" :key="secondnav._system_record_entry_id" @click="
            $router.push({
              path: '/searchresult',
              query: { keyword: secondnav.title },
            })
          ">
            <img v-lazy="secondnav?.image" />
            <p>{{ secondnav?.title }}</p>
          </li>
        </ul>

        <ul class="sortFilter">
          <li v-for="sortF in list?.sortFilter" :key="sortF.sortKey">
            <p :class="sortF.sortKey === sort ? 'active' : ''" @click="sort = sortF?.sortKey">
              {{ sortF?.title }}
            </p>
          </li>
        </ul>

        <van-list v-if="l.length" v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad"
          class="wall">
          <div class="list" v-for="d in l" :key="d.iid"
            @click="$router.push({ path: '/commodity', query: { id: d.iid } })">
            <div class="img">
              <img v-lazy="d?.img" />
              <span>已售{{ d?.sale }}</span>
            </div>
            <div class="msg">
              <p>{{ d?.title }}</p>
              <div class="num">
                <span>&yen;{{ d?.price }}</span>
                <span>{{ d?.cfav }}</span>
              </div>
            </div>
          </div>
        </van-list>
      </div>
    </div>

    <SearchSuggest v-if="showSearchSuggest" @exit-searchsuggest="showSearchSuggest = $event" />
  </div>
</template>

<script>
import { mapState } from "vuex";
import SearchSuggest from "@/components/SearchSuggest.vue";
export default {
  components: {
    SearchSuggest,
  },
  data() {
    return {
      pid: 3627,
      fcid: 10062603,
      page: 1,
      sort: "pop",
      activeKey: 0,
      showSearchSuggest: false,

      list: {},
      loading: false,
      finished: false,
      l: [],
    };
  },
  created() {
    this.$store.dispatch("getClassifyMainNav");
    this.$store.dispatch("getClassifySecondNav", this.pid);
    this.axios
      .get("https://apis.netstart.cn/mogujie/category/miniWall", {
        params: {
          fcid: this.fcid,
          page: this.page,
          sort: this.sort,
          cKey: "h5-cube",
        },
      })
      .then((res) => {
        this.list = res.data.result;
        this.loading = false;
        this.l = [...this.l, ...this.list.wall.docs];
      });
  },
  computed: {
    ...mapState(["classifyMainNav", "classifySecondNav", "classifyMiniwall"]),
  },
  methods: {
    onLoad() {
      this.page++;

      this.axios
        .get("https://apis.netstart.cn/mogujie/category/miniWall", {
          params: {
            fcid: this.fcid,
            page: this.page,
            sort: this.sort,
            cKey: "h5-cube",
          },
        })
        .then((res) => {
          this.list = { ...this.list, ...res.data.result };
          this.l = [...new Set(JSON.stringify([...this.l, ...this.list.wall.docs])).map(o => JSON.parse(o))];

          this.loading = false;

          if (this.l.length >= this.list.wall.total) {
            this.finished = true;
          }
        });
    },
  },

  watch: {
    sort: function (newitem) {
      this.axios
        .get("https://apis.netstart.cn/mogujie/category/miniWall", {
          params: {
            fcid: this.fcid,
            page: this.page,
            sort: newitem,
            cKey: "h5-cube",
          },
        })
        .then((res) => {
          this.list = res.data.result;
          this.loading = false;
          this.l = [...this.list.wall.docs];
        });
    },
    fcid: function (newitem) {
      this.axios
        .get("https://apis.netstart.cn/mogujie/category/miniWall", {
          params: {
            fcid: newitem,
            page: this.page,
            sort: newitem,
            cKey: "h5-cube",
          },
        })
        .then((res) => {
          this.list = res.data.result;
          this.loading = false;
          this.l = [...this.list.wall.docs];
        });
    },
    pid: function (newitem) {
      this.$store.dispatch("getClassifySecondNav", newitem);
    },
  },
};
</script>

<style lang="less" scoped>
.classify {
  .search {
    display: flex;
    align-items: center;
    padding: 10rem 0;
    border-bottom: 1rem solid rgba(0, 0, 0, 0.1);

    .im-talk {
      width: 50rem;
      height: 30rem;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .input {
      width: 265rem;
      height: 30rem;
      display: flex;
      align-items: center;
      background: #eeeeee;

      img {
        width: 15rem;
        height: 15rem;
        margin: 0 5rem;
      }

      input[type="text"] {
        width: calc(100% - 25rem);
        height: 30rem;
        border: none;
        outline: none;
        background: #eeeeee;
      }
    }

    .cart {
      width: 50rem;
      height: 30rem;
      background: url(../assets/classify-cart.png) no-repeat center;
      background-size: 20rem;
    }
  }

  .nav {
    display: flex;

    .mainnav {
      height: calc(100vh - 51rem);

      &::-webkit-scrollbar {
        display: none;
      }
    }

    .content {
      width: calc(100vw - 80rem);
      height: calc(100vh - 51rem);
      overflow-y: scroll;

      ul.secondnav {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        padding: 15rem 10rem 17rem 10rem;
        border-bottom: 1rem solid rgba(0, 0, 0, 0.1);

        li {
          width: 90rem;
          display: flex;
          flex-direction: column;
          align-items: center;
          margin: 10rem 0;

          img {
            width: 65rem;
            height: 65rem;
          }

          p {
            font-size: 12rem;
            margin-top: 10rem;
          }
        }
      }

      ul.sortFilter {
        display: flex;
        border-bottom: 1rem solid rgba(0, 0, 0, 0.1);

        li {
          width: 33.33%;
          height: 40rem;
          display: flex;
          justify-content: center;
          align-items: center;

          p {
            width: 100%;
            text-align: center;
            font-size: 14rem;
            border-left: 1rem solid rgba(0, 0, 0, 0.05);

            &.active {
              color: #ef4562;
            }
          }
        }
      }

      .wall {
        display: flex;
        flex-wrap: wrap;

        div.list {
          width: 47%;
          border-radius: 2rem 2rem 0 0;
          margin: 5rem 0 0 6rem;

          .img {
            position: relative;
            width: 100%;
            height: 180rem;
            overflow: hidden;
            background: #e5e5e5;

            img {
              border-radius: 2rem 2rem 0 0;
            }

            span {
              display: block;
              background: url(../assets/classify-textbg.png) left center no-repeat;
              background-size: 100%;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
              height: 25rem;
              line-height: 25rem;
              color: white;
              position: absolute;
              bottom: 5rem;
              left: 0;
              padding: 0 20rem 0 10rem;
            }
          }

          p {
            overflow: hidden;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            font-size: 12rem;
            color: #333333;
          }

          .msg {
            padding: 5rem 6rem 4rem 6rem;

            p {
              font-size: 12rem;
              color: #333;
            }

            .num {
              width: 100%;
              display: flex;
              justify-content: space-between;

              span {
                &:nth-child(1) {
                  color: #ff5777;
                  font-size: 15rem;
                  font-weight: bold;
                  margin-top: 3rem;
                }

                &:nth-child(2) {
                  font-size: 12rem;
                  color: #999999;
                  margin-top: 4.5rem;
                  padding-right: 15rem;
                  background: url(../assets/shoppingmall-whitestart.png) center right no-repeat;
                  background-size: auto 12rem;
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>